import React, { useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  useNavigate,
} from "react-router-dom";

import Puzzle from "./components/puzzle";
import SkyPage from "./pages/Sky";

import BgImage from "./images/BG.jpg";

const MainPage = () => {
  return (
    <div style={styles.container}>
      <Puzzle />
    </div>
  );
};

const BlackScreen = () => {
  return <div style={{ backgroundColor: "black", height: "100vh" }}></div>;
};

const StartScreen = () => {
  const navigate = useNavigate();
  const [isFirstLaunch, setIsFirstLaunch] = useState(true);

  useEffect(() => {
    const checkFirstLaunch = () => {
      const firstLaunch = localStorage.getItem("firstLaunch");
      if (firstLaunch) {
        setIsFirstLaunch(false);
      } else {
        localStorage.setItem("firstLaunch", "false");
      }
    };

    checkFirstLaunch();
  }, []);

  if (isFirstLaunch) {
    return null;
  }

  return (
    <div style={styles.container}>
      <h1 style={{ textAlign: "center" }}>    ?</h1>
      <button style={styles.button} onClick={() => navigate("/main")}>
        <b></b>
      </button>
      <button style={styles.button} onClick={() => navigate("/black")}>
        <b></b>
      </button>
    </div>
  );
};

const styles = {
  container: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
    backgroundImage: `url(${BgImage})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    color: "#fff",
  },
  button: {
    backgroundColor: "#FF69B4",
    padding: "12px 24px",
    minWidth: "100px",
    margin: "10px",
    border: "none",
    color: "white",
    fontSize: "16px",
    cursor: "pointer",
  },
};

const App = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<StartScreen />} />
        <Route path="/main" element={<MainPage />} />
        <Route path="/black" element={<BlackScreen />} />
        <Route path="/sky-page" element={<SkyPage />} />
      </Routes>
    </Router>
  );
};

export default App;
